'use client'
import type { Metadata } from 'next'
import {
  ClerkProvider,
  SignInButton,
  SignUpButton,
  SignedIn,
  SignedOut,
  UserButton,
  useUser,
} from '@clerk/nextjs'
import { Geist, Geist_Mono } from 'next/font/google'
import { useEffect } from 'react'
import './globals.css'

const geistSans = Geist({
  variable: '--font-geist-sans',
  subsets: ['latin'],
})

const geistMono = Geist_Mono({
  variable: '--font-geist-mono',
  subsets: ['latin'],
})

export const metadata: Metadata = {
  title: 'Unibuddy',
  description: 'Syllabus â†’ Calendar Magic',
}

// REDIRECT COMPONENT
function SignedInRedirect() {
  const { isLoaded, isSignedIn } = useUser()
  
  useEffect(() => {
    if (isLoaded && isSignedIn && window.location.pathname === '/') {
      window.location.href = '/dashboard'
    }
  }, [isLoaded, isSignedIn])
  
  return null
}

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode
}>) {
  return (
    <ClerkProvider>
      <html lang="en">
        <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>
          <header className="flex justify-end items-center p-4 gap-4 h-16 bg-white/80 backdrop-blur-md border-b border-gray-100">
            <SignedOut>
              <SignInButton />
              <SignUpButton>
                <button className="bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-full font-medium text-sm sm:text-base h-10 sm:h-12 px-4 sm:px-5 shadow-lg hover:shadow-xl transition-all">
                  Sign Up
                </button>
              </SignUpButton>
            </SignedOut>
            <SignedIn>
              <UserButton />
            </SignedIn>
          </header>
          
          <SignedInRedirect />
          
          <main className="min-h-[calc(100vh-4rem)]">
            {children}
          </main>
        </body>
      </html>
    </ClerkProvider>
  )
}
